<template>
  <div class="modify" >
   <h1 class="title">账户信息: </h1>
   <hr>
   <el-card class="modify-box">
           <div slot="header" class="clearfix">
            <span>提示:请确认账号信息后,再进行修改!</span>
          </div>
      <el-form  label-width="120px" class="form"  :disabled="true">
            <el-form-item label="用户ID">
              <el-input v-model="user.userid"></el-input>
            </el-form-item>
             <el-form-item label="用户姓名:">
                 <el-input v-model="user.name"></el-input>
            </el-form-item>
      </el-form>
     <el-form ref="form" :model="form" label-width="120px"  class="modifyCount">
       <el-form-item label="修改密码">
          <el-switch v-model="form.delivery"></el-switch>
          <el-form  v-show="this.form.delivery==true?true:false" :model="form" status-icon :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">
               <el-form-item label="密码" prop="pass" class="pass-item">
                    <el-input type="password" v-model="form.pass" autocomplete="off"></el-input>
               </el-form-item>
               <el-form-item label="确认密码" prop="checkPass" class="pass-item">
                     <el-input type="password" v-model="form.checkPass" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
       </el-form-item>
      <!-- <el-form-item label="上传头像">
          <el-upload
            class="upload-demo"
            drag
            action="http://127.0.0.1/api/img"
            :limit = '1'
            list-type='picture'
            accept='.jpg,.png'
            :before-upload='detectPictures'
            multiple>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
       </el-form-item> -->
      <el-form-item>
          <el-button type="primary" @click="update">保存</el-button>
          <el-button @click="cancel">取消</el-button>
      </el-form-item>
    </el-form>
   </el-card>

  </div>
</template>

<script>
import md5 from 'js-md5';
export default {
   data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.form.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.form.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
        dialogImageUrl: '',
        dialogVisible: false,
        },
       form: {
          delivery: false,
          pass: '',
          checkPass: '',
      },
        rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
        },
      user:JSON.parse(sessionStorage.getItem('userinfo'))
      };
    },
    methods: {
    detectPictures(file){
     if(file.type !='image/png' && file.type != 'image/jpeg'){
      this.$message({
         message: '请选择png或者jpg格式的图片!',
         type: 'error',
         offset:'5'
       });
       return false
     }
   },
    cancel(){
      this.form={}
    },
       out(){
        this.$axios.post('/API/outinfo',{
         }).then(res => { 
           if(res.data.code=='1'){
            sessionStorage.removeItem("userinfo");
            this.$router.replace({path:'/login'});
           }
         }).catch(err => {
             this.$alert('服务器异常,请联系管理员', '提示', {
              confirmButtonText: '确定',
              type: 'error',
            });
         })
      },
      update(){
      let param = new URLSearchParams()
      param.append('userid', this.user.userid)
      param.append('password',  md5(this.form.checkPass))
       this.$axios.post("/API/update",param).then(res => {
          if(res.data.code=='1'){
            this.$message.success(res.data.message);
            this.out();
           }else{
            this.$message.error(res.data.message);
          }
        
         }).catch(err => {
             this.$alert('服务器异常,请联系管理员', '提示', {
              confirmButtonText: '确定',
              type: 'error',
            });
        })
      }
 }
}
</script>


<style scoped>
 .form{
 width: 480px;
 }
 .table{
   width: 100%;
   display: inline-block;
 }
 .title{
   width: 100%;
   text-align: left;
   line-height: 60px;
   padding-left: 20px;
 }
  .text {
    font-size: 14px;
  }
.modifyCount{
  text-align: left;
  width: 480px;
}
.pass-item{
  margin: 15px 0;
}
.modify-box{
padding-right: 10px;
width: 550px;
margin: 20px 130px;
}
</style>
